import { ProcessDefinitionMetaDataRepresentation } from './processDefinitionMetaDataRepresentation';

export class ProcessDefinitionRepresentation {
    category?: string;
    deploymentId?: string;
    description?: string;
    hasStartForm?: boolean;
    id?: string;
    key?: string;
    metaDataValues?: ProcessDefinitionMetaDataRepresentation[];
    name?: string;
    tenantId?: string;
    version?: number;

    constructor(input?: any) {

        if (input) {
            Object.assign(this, input);
            if (input.metaDataValues) {
                this.metaDataValues = input.metaDataValues.map((item: any) => {
                    return new ProcessDefinitionMetaDataRepresentation(item);
                });
            }
        }
    }

}
